import React, { Component } from 'react';
import { connect } from 'react-redux'
// import { Link } from 'react-router-dom'
import { checkOut, getOrderDetails } from './actions/cartActions'
import PaymentInfo from './PaymentInfo'
class Cart extends Component{

    //to remove the item completely
    handleCheckOut = ()=>{
        this.props.checkOut();
    }
    handleGetOrderDetails = () => {
        this.props.getOrderDetails()
    }
    render(){

        let items = this.props.items.length ?
            (
                this.props.items.map(item=>{
                    return(
                        <li className="collection-item avatar" key={item.id}>
                            <div className="item-img">
                                <img src={item.img} alt={item.img} className=""/>
                            </div>

                            <div className="item-desc">
                                <span className="title">{item.title}</span>
                                <p>{item.desc}</p>
                                <p><b>Price: {item.price}$</b></p>
                                <p>
                                    <b>Quantity: {item.quantity}</b>
                                </p>
                            </div>
                        </li>
                    )
                })
            ):

             (
                <p> You have not ordered anything yet</p>
             )
       return(
            <div className="container">
                <div className="cart">
                    <h3>Order Summary</h3>
                    <ul className="collection">
                        {items}
                    </ul>
                </div>
                <div className="collection">
                    <li className="collection-item"><b>Total: {this.props.total} $</b></li>
                </div>
                <h3>Payment Info</h3>
                < PaymentInfo />
                <div className="checkout">
                    <button className="waves-effect waves-light btn" handleClick={this.handleCheckOut}>Checkout</button>
                    <button className="waves-effect waves-light btn" style={{marginLeft: '15px'}}>Refresh</button>
                </div>
            </div>
       )
    }
}


const mapStateToProps = (state)=>{
    return{
        // items: state.addedItems,
        items: state.items,
        total: state.total
    }
}
const mapDispatchToProps = (dispatch)=>{
    return{
        checkOut: ()=>{dispatch(checkOut())},
        getOrderDetails:()=>{dispatch(getOrderDetails())},
    }
}
export default connect(mapStateToProps,mapDispatchToProps)(Cart)