import React, { Component } from 'react';
import { connect } from 'react-redux'

class HomePage extends Component{
    render() {
        return(
        <div className="container" style={{width:"500px"}}>
            <h3>Enter Your Table Number</h3>
            <div class="row">
                <form class="col s12">
                    <div class="row">
                        <div class="input-field col s12">
                            <input id="table-num" type="text" class="validate" />
                            <label for="table-num"></label>
                        </div>
                    </div>
                </form>
            </div>
            <button className="waves-effect waves-light btn" style={{marginLeft: '15px'}}>Submit</button>
        </div>
        )
    }
}

const mapStateToProps = (state)=>{
    return{
        items: state.items,
        total: state.total
    }
}
const mapDispatchToProps = (dispatch)=>{
    return{
        // checkOut: ()=>{dispatch(checkOut())},
        // getOrderDetails:()=>{dispatch(getOrderDetails())},
    }
}
export default connect(mapStateToProps,mapDispatchToProps)(HomePage)